.mainWrap {
  .totalWrap {
    display: flex;
    justify-content: space-around;
    padding: 24px;
    background-color: #f0f2f5;
    //  align-items: center;

    .week,
    .today,
    .total,
    .like {
      flex-basis: 300px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 200px;
      padding: 20px;
      background: #fff;
      box-shadow: rgb(174, 174, 174) 0px 0px 8px;
      color: rgba(0, 0, 0, 0.45);
      border-radius: 10px;
    }
    .week {
      .title {
        color: rgba(0, 0, 0, 0.45);
        font-size: 14px;
        line-height: 22px;
      }
      .counts {
        text-align: left;
        color: #f39c12;
        height: 34px;
        margin-top: 4px;
        margin-bottom: 10px;
        font-size: 30px;
      }
      .weekChart {
        width: 100%;
        height: 30px;
      }
      .rateWrap {
        text-align: right;
        span {
          font-size: 20px;
          color: #9b59b6;
        }
      }
    }
    .today {
      .counts {
        // text-align: right;
        font-size: 30px;
        color: #2980b9;
      }
      .todayChart {
        height: 50px;
      }
      .rateWrap {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .upRate {
          font-size: 24px;
          color: #e74c3c;
        }
        .cutRate {
          font-size: 24px;
          color: #27ae60;
        }
        .upIcons {
          color: #e74c3c;
        }
        .cutIcons {
          color: #27ae60;
        }
      }
    }
    .total {
      .counts {
        font-size: 24px;
        color: #95a5a6;
        .upSize {
          font-size: 60px;
          color: #e74c3c;
        }
      }
    }
    .like {
      .totalCharts {
        width: 100%;
        height: 160px;
      }
    }
  }

  .latestWrap {
    display: flex;
    align-content: space-between;
    padding: 24px;
    // margin: 24px;
    .viewWrap {
      width: 500px;
      .viewsTitle {
        margin-bottom: 5px;
        color: rgba(0, 0, 0, 0.85);
      }
      .viewsList {
        padding: 10px 0;
        color: rgba(0, 0, 0, 0.65);
        font-size: 14px;
        line-height: 22px;
        .index {
          color: #c0392b;
        }
        .title {
          width: 180px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap; /*规定段落中的文本不进行换行 */
        }
        .counts {
          text-align: right;
        }
      }
    }
    .barWrap{
      width:  60%;
      // flex: 1;
      // margin: 0 25px;
    }
  }

  .lineWrap {
    // width: 95%;
    // margin: 15px auto;
    padding: 20px;
    border-bottom: 20px solid #f0f2f5;
    background-color:  #fff;
    .lineChart {
      width: 100%;
      height: 280px;
    }
  }
  
}
